import React, { useEffect, Fragment } from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { getCurrentProfile, deleteAccount } from '../../actions/profile';
import Spinner from '../layout/Spinner';
import DashboardActions from './DashboardActions';
import Experience from '../dashboard/Experience';
import Education from '../dashboard/Education';

const Dashboard = ({
  getCurrentProfile,
  auth: { user },
  profile: { profile, loading },
  status,
  deleteAccount,

}) => {
  useEffect(() => {
    getCurrentProfile();
  }, [getCurrentProfile]);
 
  let show=(profile)=>{
    if( profile.status==="faculty"){
      return (

        <Link to={'/posts'}>
        <button style={{width:"70%",marginTop:"2%", height:"20%" , fontSize:"30px"}} className='btn btn-success'>Courses (Faculty access) Click here </button>
        </Link>
      )
    }
    else{
      return (
        <Link to={'/posts/student'}>
        <button style={{width:"70%",marginTop:"2%", height:"20%" , fontSize:"30px"}} className='btn btn-success'> Student (Student access)</button>
        </Link>)
    }
  }


  return loading && profile === null ? (
    <Spinner />
  ) : (
    <Fragment>
      <h1 className='large text-primary  '>Dashboard</h1>
        {getCurrentProfile}
      <p className='lead'>
        {console.log("profile",profile)}
        <i className='fas fa-user'></i> Welcome {user && user.name} 
          {}
      </p>

      {profile !== null ? (
        <Fragment>
          {' '}
          <DashboardActions />
          {show(profile)}
          <Experience experience={profile.experience} />
          <Education education={profile.education} />
       

          <div className='my-2'>
            <button
              className='btn btn-danger'
              onClick={() => {
                deleteAccount();
              }}>
              <i className='fas fa-user-minus'></i>
              Delete My Account
            </button>
          </div>
        </Fragment>
      ) : (
        <Fragment>
          <p>You have not yet setup a profile, please add some info</p>
          <Link to='/create-profile' className='btn btn-primary my-1'>
            {' '}
            Create Profile
          </Link>
        </Fragment>
      )}
    </Fragment>
  );
};

Dashboard.propTypes = {
  getCurrentProfile: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  profile: PropTypes.object.isRequired,
  deleteAccount: PropTypes.func,
};

const mapStateToProps = (state) => ({
  auth: state.auth,
  profile: state.profile,
});

export default connect(mapStateToProps, { getCurrentProfile, deleteAccount })(
  Dashboard
);
